<template>
  <div class="flex-column">
    <ul class="comment-list">
      <li
      v-for="item in data" :key="item.createdAt"
      class="mt-1"
      >
        <div class="flex-row">
          <img 
          :src="item.avatar" alt="user avatar"
          class="comment-avatar"
          >
          <div class="ml-1 flex-1">
            <span class="comment-username">{{ item.username }}</span>
            <span class="comment-time">{{ item.createdAt | formatDateTime }}</span>
            <div class="comment-body">{{ item.content }}</div>
          </div>
        </div>
      </li>
    </ul>
    <form action="" class="pure-form pure-form-stacked" @submit.prevent="submit">
      <textarea class="w-100" name="comment-input" id="comment-input" cols="30" rows="8"
      minlength="10"
      :disabled="flag"
      >
      </textarea>
      <button class="pure-button w-100" :disabled="flag">
        Post
      </button>
    </form>
  </div>
</template>

<script>

export default {
  props: {
    data: {
      type: Array,
      required: true,
    },
    flag: {
      type: Boolean,
      default: false,
    }
  },
  methods: {
    submit() {
      if (this.flag) {
        return;
      }
      const el = document.getElementById('comment-input');
      if (el.value.trim()) {
        let user = window.$user;
        const payload = {
          username: user.username,
          avatar: user.avatar,
          content: el.value.trim(),
        }
        el.value = '';
        this.$emit('submit', payload);
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.comment-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.comment-avatar {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

.comment-time {
  margin-left: 0.4em;
  font-size: 0.8em;
  color: #777;
}

.comment-body {
  white-space: pre-line;
  word-break: break-all;
}
</style>
