<template>
  <div class="rating">
    <div class="flex-row">
      <strong class="rating-score">
        {{ data.score }}
      </strong>
      <small class="text-light ml-1">
        {{ data.voteCnt }}人评价
      </small>
    </div>
    <ul class="rating-body mt-1">
      <li class="rating-item" v-for="item in data.list" :key="item.star">
        {{ item.star }}星: 
        <span class="rating-bar">
        {{ '&nbsp;'.repeat(Math.floor(item.percent * 30)) }}
        </span>
        {{ item.count }} 人
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    }
  }
}
</script>

