<template>
  <div class="calender-container">
    <span class="calender" @click="toClick">
      <span class="calender-item"
      v-for="(item, index) in list"
      :key="item"
      :data-date="item"
      :data-index="index"
      :data-cnt="10"
      :class="{'calender-item-active':  active === index}"
      >
      </span>
    </span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [

      ],
      active: -1,
    }
  },
  mounted() {
    this.list = this.getData();
  },
  methods: {
    getData() { 
      const year = 24 * 60 * 60 * 100 * 365 * 1000;
      const DAY = 24 * 60 * 60 * 1000;
      let today = new Date().getTime();
      let lastYear = today - year;
      let dates = [];
      while(dates.length < 365) {
        dates.unshift(today);
        today -= DAY;
      }
      let firstDay = new Date(today);
      while(firstDay.getDay() !== 1) {
        today -= DAY;
        dates.unshift(today);
        firstDay = new Date(today);
      }
      return dates.map((obj) => {
        return new Date(obj).toLocaleDateString();
      });
    },
    toClick(event) {
      const el = event.target;
      const index = el.getAttribute('data-index');
      console.log(index);
      if (index === null) {
        this.active = -1;
      } else {
        this.active = +index;
      }
    }
  }
}
</script>

<style lang="scss" scoped>
$width: 10px;
$gap: 2px;
$cnt: 365;

.calender-container {
  width: $width * ($cnt / 7 + 1) + $gap * ($cnt / 7);
  height: $width * 7 + $gap * 6;
  position: relative;
  align-self: center;
  margin-top: 2em;
}
.calender {
  display: grid;
  writing-mode: vertical-lr;
  // transform: rotateZ(90deg);
  grid-template-columns: repeat(7, $width);
  grid-gap: $gap;
  grid-auto-rows: $width;
  width: $width * ($cnt / 7 + 1) + $gap * ($cnt / 7);
  height: $width * 7 + $gap * 6;

  .calender-item {
    width: $width;
    height: $width;
    background: silver;
    position: relative;
    &:hover {
      background: orange;
      &::after {
        content: '';
        writing-mode: horizontal-tb;
        // transform: rotateZ(-90deg);
        border-left: 5px solid #ffffff00;
        border-right: 5px solid #ffffff00;
        border-bottom: 5px solid #ffffff00;
        border-top: 5px solid #ccc;
        display: inline-block;
        z-index: 999;
        top: -5px;
        position: absolute;
      }
      &::before {
        content: attr(data-cnt) 'activites ' attr(data-date);
        position: absolute;
        writing-mode: horizontal-tb;
        // transform: rotateZ(-90deg);
        font-size:  12px;
        line-height:  14px;
        z-index: 9999;
        padding: 2px;
        border: 1px solid silver;
        background-color: #ccc;
        top: -38px;
      }
    }
  }
  .calender-item-active {
    background: orange;
  }
}
</style>
