<template>
  <div class="sidebar">
    <ul class="sidebar-list h-100">
      <li class="sidebar-list-item" 
      v-for="(tab, i) in tabs"
      :key="tab.name"
      :class="{'sidebar-list-item-active': active === i, 'sidebar-list-item-saved': status[i]}"
      @click="toClick(i)"
      >
      {{ tab.name }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    tabs: {
      type: Array,
      default: [],
    },
    status: {
      type: Array,
      default: [],
    },
    active: {
      type: Number,
      default: 0,
    }
  },
  methods: {
    toClick(index) {
      this.$emit('click', index);
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/assets/css/box.scss';
@import '@/assets/css/padding.scss';
@import '@/assets/css/colors.scss';

.sidebar {
  @extend .shadow-1;
  z-index: 9999;
}

.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-list-item {
  @extend .pd-1;

  display: block;
  width: 10em;
  line-height: 2em;
  text-align: center;
  color: #777;
  &:hover {
    cursor: pointer;
    background: #ccc;
  }
}
.sidebar-list-item-active {
  background: #ccc;
}

.sidebar-list-item-saved {
  color: white;
  background: $success;
  &:hover {
    background: #eee;
    color: #777;
  }
}
</style>
