      </small>
      <!-- <small class="text-center mt-auto text-light ml-auto ">
        <span class="label">
          Autho
        </span>
        {{ data.author }}
      </small>
      <small class="date ml-auto text-light">
        {{ data.createdAt | formatDateTime }}
      </small>
      <small class="text-center text-light ml-auto ">
        {{ data.category.toString() }}
      </small> -->
    </div>
    <div class="inner-1 absolute">
    </div>
    <div class="inner-2 absolute">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    }
  }
}
</script>

<style lang="scss" scoped>
.card-group-cover {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin: 0 auto;
}
.meta {
  color: #777;
}
.label {
