import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/tests',
    },
    {
      path: '/tests',
      name: 'tests',
      component: () => import(/* webpackChunkName: "Tests" */ './views/Tests/List/index.vue'),
    },
    {
      path: '/tests/:title',
      name: 'test',
      props: true,
      component: () => import(/* webpackChunkName: "Test" */ './views/Tests/Test/index.vue'),
    },
   {
      path: '/cards',
      name: 'cards',
      component: () => import(/* webpackChunkName: "cards" */ './views/Cards/List/index.vue'),
    },
    {
      path: '/cards/:title',
      name: 'card',
      props: true,
      component: () => import(/* webpackChunkName: "card" */ './views/Cards/Card/index.vue'),
    },
    {
      path: '/testing/:title',
      name: 'testing',
      props: true,
      component: () => import(/* webpackChunkName: "testing" */ './views/Tests/Testing/index.vue'),
    },
    {
      path: '/results/:id',
      name: 'results',
      component: () => import(/* webpackChunkName: "results" */ './views/Tests/Result/index.vue'),
    },
    {
      path: '/practising/:title',
      name: 'practising',
      props: true,
      component: () => import(/* webpackChunkName: "practising" */ './views/Cards/Practising/index.vue'),
    },
    {
      path: '/workshop/tests',
      name: 'workshopTests',
      component: () => import(/* webpackChunkName: "workshopTests" */ './views/Workshop/Tests/index.vue'),
    },
    {
      path: '/workshop/cards',
      name: 'workshopCards',
      component: () => import(/* webpackChunkName: "workshopCards" */ './views/Workshop/Cards/index.vue'),
    },
    {
      path: '/profile/:username',
      name: 'profile',
      props: true,
      component: () => import(/* webpackChunkName: "profile" */ './views/Profile'),
    },
    {
      path: '/requests',
      name: 'requests',
      component: () => import(/* webpackChunkName: "requests" */ './views/Requests/List'),
    },
    {
      path: '/requests/:title',
      name: 'request',
      props: true,
      component: () => import(/* webpackChunkName: "requests" */ './views/Requests/Request'),
    },
    {
      path: '/login',
      name: 'login',
      component: () => import(/* webpackChunkName: "login" */ './views/Login.vue'),
    },
    {
      path: '/add/request',
      name: 'add-request',
      component: () => import(/* webpackChunkName: "add-request" */ './views/Forms/request.vue'),
    },
    {
      path: '/add/post',
      name: 'add-post',
      component: () => import(/* webpackChunkName: "add-post" */ './views/Forms/post.vue'),
    },
    {
      path: '/posts',
      name: 'posts',
      component: () => import(/* webpackChunkName: "posts" */ './views/Posts/List/index.vue'),
    },
    {
      path: '/posts/:title',
      name: 'post',
      props: true,
      component: () => import(/* webpackChunkName: "post" */ './views/Posts/Post/index.vue'),
    },
    {
      path: '/register',
      name: 'register',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "register" */ './views/Register.vue'),

    },

  ],
});

router.beforeEach((to, from, next) => {
  if (to.name === 'testing' || to.name === 'practising') {
    window.$mode = 'testing';
  } else {
    window.$mode = 'normal';
  }
  next();
})

export default router; 
