export default {
  methods: {
    keepLinebreak(text) {
      return text.replace(/(?:\r\n|\r|\n)/g, '<br>');
    },
    deepCopy(obj) {
      return JSON.parse(JSON.stringify(obj));
    },
  },
  data() {
    return {
      QUESTION_TYPE: {
        SINGLE_CHOICE: 'single-choice',
        MULTIPLE_CHOICE: 'multiple-choice',
        BLANK_FILLING: 'blank-filling',
      },
      REQUEST_APPROVED: 0,
      REQUEST_UNAPPROVED: 1,
    }
  }
}