import Vue from 'vue';
import upperFirst from 'lodash/upperFirst'
import camelCase from 'lodash/camelCase'
import axios from 'axios';
import VueLazyload from 'vue-lazyload'

import App from './App.vue';
import router from './router';
import mixin from './mixins';
import url from './config/index';

const constants = {
  EACH: 0,
  ALL: 1,
};

Vue.use(VueLazyload);

Vue.mixin(mixin);

// load components
const requireComponent = require.context(
  './components',
  true,
  /\w+\.(vue|js)$/
)

requireComponent.keys().forEach(fileName => {
  const componentConfig = requireComponent(fileName)
  const componentName = upperFirst(
    camelCase(
      fileName.replace(/^\.\/(.*)\.\w+$/, '$1')
    )
  )

  console.log(componentName);
  Vue.component(
    componentName,
    componentConfig.default || componentConfig
  )
})

// load directives
const requireDirective = require.context(
  './directives',
  true,
  /\w+\.js$/
)
requireDirective.keys().forEach(fileName => {
  const directiveConfig = requireDirective(fileName)
  const directiveName = upperFirst(
    camelCase(
      fileName.replace(/^\.\/(.*)\.\w+$/, '$1')
    )
  )

  Vue.directive(
    directiveName,
    directiveConfig.default || directiveConfig
  )
})

// load filters
const requireFilters = require.context(
  './filters',
  // Whether or not to look in subfolders
  true,
  // The regular expression used to match base component filenames
  /\w+\.js$/
)
requireFilters.keys().forEach(fileName => {
  // Get config
  const filterConfig = requireFilters(fileName)
  // Get PascalCase name of filter
  const filterName = upperFirst(
    camelCase(
      // Strip the leading `./` and extension from the filename
      fileName.replace(/^\.\/(.*)\.\w+$/, '$1')
    )
  )

  // Register filter globally
  Vue.filter(
    filterName,
    // Look for the filter options on `.default`, which will
    // exist if the filter was exported with `export default`,
    // otherwise fall back to module's root.
    filterConfig.default || filterConfig
  )
})


Vue.config.productionTip = false;

function alert (type, message)  {
  const delay = 1300;
  const div = document.createElement('div');
  div.innerHTML = message;
  div.classList.add('alert');
  if (type === 'success') {
    div.classList.add('alert-success');
    const icon = document.createElement('i');
    icon.classList.add('fas', 'fa-check-circle');
    div.prepend(icon);
  } else if (type === 'error') {
    div.classList.add('alert-error');
    const icon = document.createElement('i');
    icon.classList.add('fas', 'fa-exclamation-triangle');
    div.prepend(icon);
  }
  document.body.appendChild(div);
  setTimeout(() => {
    document.body.removeChild(div);
  }, delay);
};

Vue.prototype.$alert = alert;

// Add a response interceptor
axios.interceptors.response.use(function (response) {
    // Do something with response data
    return response;
  }, function (error) {
    // Do something with response error
    // return Promise.reject(error);
    alert('error', error.response.data || error.message || '发生未知错误');
  });

Vue.prototype.$axios = axios;
Vue.prototype.$url = url;
Vue.prototype.$constants = constants;

Vue.prototype.$debounceDelay = 300;

Vue.prototype.$TYPES = {
  SINGLE_CHOICE: 'single-choice',
  MULTIPLE_CHOICE: 'multiple-choice',
  OPENING: 'opening',
  BLANK_FILLING: 'blank-filling',
};



Vue.prototype.$bus = new Vue();

new Vue({
  router,
  render: h => h(App),
}).$mount('#app');
