<template>
  <div class="radio-container flex-row">
    <input type="radio" class="radio" :name="name" :id="id"
    @change="change"
    :disabled="disabled"
    :checked="checked"
    >
    <label class="radio-label" :for="id">
    </label>
    <span class="flex-1 radio-label-body break-all">
      {{ message }}
    </span>
  </div>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
    },
    id: {
      type: String,
    },
    message: {
      type: String,
    },
    disabled: {
      type: Boolean,
    },
    checked: {
      type: Boolean,
    },
  },
  methods: {
    change(event) {
      this.$emit('change', event);
    },
  },
};
</script>

