<template>
  <span class="breadcrumb">
    <span
      v-if="!readonly"
      class="breadcrumb-item"
      @click="toClick(-1)"
    >
    </span>
    <span
     class="breadcrumb-item" v-for="(item, index) in data" :key="item"
     :class="{'readonly': readonly}"
     @click="toClick(index)"
     >
      {{ item }}
    </span>
  </span>
</template>

<script>
export default {
  props: {
    data: {
      type: Array,
      required: true,
    },
    readonly: {
      type: Boolean,
      default: false,
    }
  },
  methods: {
    toClick(index) {
      if (this.readonly) {
        return;
      }
      this.$emit('click', index);
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/assets/css/colors.scss';

.breadcrumb-item:nth-child(n) {
  color: $primary;
  font-size: 0.8em;
  &:hover:not(.readonly) {
    cursor: pointer;
    color: darken($primary, 10%);
  }
  &::after {
    content: '/';
  }
}
</style>
