<template>
  <div class="tree-container w-100">
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      table: {},
    }
  },
  watch: {
    data() {
      const root = document.getElementsByClassName('tree-container')[0];
      this.build(root, this.data, 0);
      this.buildTable(this.data);
    }
  },
  methods: {
    buildTable() {
      const dfs = (root) => {
        if (!root) {
          return;
        }
        let arr = root;
        while (arr.length > 0) {
          const tmp = [];
          arr.forEach((node) => {
            node.children.forEach((child) => {
              this.table[child.name] = node;
              tmp.push(child);
            });
          });
          arr = tmp;
        }
      };
      dfs(this.data);
    },
    toClick(node, event) {
      event.stopPropagation();
      let crawl = node;
      const result = [crawl.name];
      while (this.table[crawl.name]) {
        crawl = this.table[crawl.name];
        result.push(crawl.name);
      }
      this.$emit('click', result.reverse());
    },
    build(root, data, level) {
      if (!root) {
        return;
      }
      if (data.length === 0) {
        return;
      }
      data.forEach((node) => {
        const div = document.createElement('div');
        div.classList.add('tree-node');
        if (level) {
          div.style.marginLeft = '1em';
        }

        const text = document.createElement('span');
        text.innerHTML = node.name;
        text.title = node.name;
        text.onclick = this.toClick.bind(this, node);

        div.appendChild(text);
        root.appendChild(div);

        if (node.children.length > 0) {
          const control = document.createElement('i');
          control.classList.add('tree-control', 'fas', 'fa-arrow-down');
          control.onclick = this.toggle.bind(this, node, level);
          text.appendChild(control);

          const son = document.createElement('div');
          div.appendChild(son);
          this.build(son, node.children, level + 1);
        }
      });
    },
    toggle(node, level, event) {
      event.stopPropagation();
      const root = event.target.parentNode.parentNode;

      if (root.children.length > 1) {
        event.target.classList.remove('fa-arrow-down');
        event.target.classList.add('fa-arrow-right');
        root.removeChild(root.lastChild);
      } else if (node.children.length > 0) {
        event.target.classList.remove('fa-arrow-right');
        event.target.classList.add('fa-arrow-down');
        const son = document.createElement('div');
        root.appendChild(son);
        this.build(son, node.children, level + 1);
      }
    },
  },
};
</script>

<style lang="scss" >

.tree-node {
  display: flex;
  flex-direction: column;

  span {
    display: inline-block;
    position: relative;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    padding: 0.4em 0.6em;
    color: #777;
    font-size: 0.8em;

    &:hover {
      cursor: pointer;
      background-color: #ddd;
    }
    .tree-control {
      position: absolute;
      right: 0.6em;
      padding: 0;
      font-size: 0.5em;
      vertical-align: middle;
    }
  }
}
</style>
