<template>
  <div id="app" class="flex-column" @click="toClickOutside">
    <nav v-if="mode === 'normal'" class="navbar pure-menu pure-menu-horizontal flex-row">
      <ul class="pure-menu-list">
        <li
        v-for="tab in tabs"
        :key="tab.name"
        class="pure-menu-item"
        >
        <router-link :to="tab.route" class="pure-menu-link">{{ tab.name }}</router-link>
        </li>
      </ul>
      <ul class="pure-menu-list ml-auto">
        <li v-if="login" class="pure-menu-item relative ">
          <div class="pure-menu-link dropdown__hook relative"
          @click="dropdown.show = !dropdown.show"
          id="nav-username"
          >
            {{ user.username }}
          </div>
          <ul v-if="dropdown.show" class="absolute dropdown" id="nav-username-dropdown">
            <li class="dropdown__item"
              @click="dropdown.show = false"
            >
              <router-link 
              :to="'/profile/' + user.username" 
              >个人信息</router-link>
            </li>
            <li class="dropdown__item"  @click="dropdown.show = false">
              <router-link to="/posts" >
              讨论
              </router-link>
            </li>
            <div class="dropdown__divisor"></div>
            <li class="dropdown__item" @click="dropdown.show = false">
              <a href="#" class="" @click="toLogout">退出</a>
            </li>
          </ul>
        </li>
        <li v-else class="pure-menu-item">
          <router-link to="/Login" class="pure-menu-link">登陆</router-link>
        </li>
      </ul>
    </nav>
    <router-view 
      class="relative flex-1"
    />
</div>
</template>

<script>

export default {
  data() {
    return {
      mode: window.$mode || 'normal',
      login: false,
      user: {},

      tabs: [
        {
          name: '测试',
          route: '/tests',
        },
        {
          name: '卡片',
          route: '/cards',
        },
        {
          name: '请求',
          route: '/requests',
        }
      ],
      dropdown: {
        show: false,
      }
    };
  },
  mounted() {
    let user = localStorage.getItem('user');
    if (user) {
      user = JSON.parse(user);
      this.login = true;
      this.user = user;
      window.$user = user;
      const jwt = localStorage.getItem('jwt');
      this.$axios.defaults.headers.common['Authorization'] = `Bearer ${jwt}`;
    }
    this.$bus.$on('changeMode', this.toChangeMode);
    this.$bus.$on('login', this.toLogin);
  },
  destroyed() {
    this.$bus.$off('changeMode', this.toChangeMode);
    this.$bus.$off('login', this.toLogin);
  },
  methods: {
    toChangeMode(mode) {
      this.mode = mode;
      localStorage.setItem('mode', mode);
    },
    toLogin() {
      this.login = true;
      this.user = window.$user;
    },
    toLogout() {
      localStorage.clear();
      this.login = false;
      this.$axios.defaults.headers.common['Authorization'] = '';
    },
    toClickOutside(event) {
      const el1 = document.getElementById('nav-username');
      const el2 = document.getElementById('nav-username-dropdown');
      if (el1 && el2) {
        const rect = el1.getBoundingClientRect();
        const rect2 = el2.getBoundingClientRect();
        const x = event.clientX;
        const y = event.clientY;
        if (x < rect.left || x > rect.right || y < rect.top || y > rect.bottom) {
          if (x < rect2.left || x > rect2.right || y < rect2.top || y > rect2.bottom) {
            this.dropdown.show = false;
          }
        }
      }
    }
  },
};
</script>

