#app {
  background: #eee;
  min-height: 100vh;
  min-width: 900px;
  .navbar {
    background: black;
    .dropdown__hook {
      &:hover {
        cursor: pointer;
      }
    }
    .dropdown {
      z-index: 1000;
      background: #fff;
      padding: 0.6em 0;
      right: 0;
      left: auto;
      .dropdown__item {
        position: relative;
        &:hover {
          background: #eee;
          cursor: pointer;
        }
        &:active {
          background: #2780e3;
          a {
            color: white;
          }
        }
        a {
          display: inline-block;
          width: 100%;
          padding: 0.4em 0.6em;
          text-decoration: none;
          color: #212529;
          white-space: nowrap;
          box-sizing: border-box;
        }
      }
      .dropdown__divisor {
        height: 0;
        border-top: 1px solid silver;
        margin: 0.4em 0;
        overflow: hidden;
      }
    }
  }
}
</style>