<template>
  <div class="relative">
    <input
    class="w-100"
    autocomplete="off"
    id="tree-input" @focus.prevent="toFocus" 
    required
    />
    <div v-if="show" id="tree-input-popout" class="shadow-1 relative">
      <div class="tree-input-popout-panel">
        <span
        @click="getData(item)" @mouseover="clickNode(item, $event)"
        :class="{'tree-input-arrow': item.children.length > 0}"
        class="pd-1 tree-input-node" v-for="item in data" :key="item.name">
          {{ item.name }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import debounce from '@/tools/debounce';

export default {
  props: {
    show: {
      type: Boolean,
    },
    data: {
      type: Array,
      required: true,
    },
    defaultValue: {
      type: String,
      required: true,
    }
  },
  mounted() {
    const el = document.getElementById('tree-input');
    el.onkeydown = (e) => e.preventDefault();
  },
  data() {
    return {
      table: {},
    };
  },
  watch: {
    data(value) {
      this.build();
    },
    defaultValue(value) {
      if (value) {
        const el = document.getElementById('tree-input');
        el.value = value;
      }
    }
  },
  methods: {
    toFocus(event) {
      if (event.target === document.activeElement) {
        this.$emit('focus');
      }
    },
    build() {
      const dfs = (root) => {
        if (!root) {
          return;
        }
        let arr = root;
        while (arr.length > 0) {
          const tmp = [];
          arr.forEach((node) => {
            node.children.forEach((child) => {
              this.table[child.name] = node;
              tmp.push(child);
            });
          });
          arr = tmp;
        }
      };
      dfs(this.data);
    },
    getData(node) {
      let crawl = node;
      const result = [crawl.name];
      while (this.table[crawl.name]) {
        crawl = this.table[crawl.name];
        result.push(crawl.name);
      }
      console.log(node, result, this.table);
      document.getElementById('tree-input').value = result.toString();
      this.$emit('change', result.toString());
    },
    clickNode: debounce(function (node, event) {
      if (!node.children) {
        return;
      }
      const root = document.getElementById('tree-input-popout');
      const prev = event.target.parentNode;
      if (prev.nextSibling) {
        if (node.children.length === 0) {
          while (prev.nextSibling) {
            root.removeChild(prev.nextSibling);
          }
          return;
        }
        const panel = prev.nextSibling;
        while (panel.nextSibling) {
          root.removeChild(panel.nextSibling);
        }
        while (panel.firstChild) {
          panel.removeChild(panel.firstChild);
        }
        for (let i = 0; i < node.children.length; i += 1) {
          const child = document.createElement('span');
          child.innerHTML = node.children[i].name;
          child.onmouseover = this.clickNode.bind(this, node.children[i]);
          child.onclick = this.getData.bind(this, node.children[i]);
          if (node.children[i].children.length > 0) {
            child.classList.add('tree-input-arrow');
          }
          child.classList.add('tree-input-node');
          child.title = node.children[i].name;
          panel.appendChild(child);
        }
      } else {
        if (node.children.length === 0) {
          return;
        }
        const panel = document.createElement('div');
        for (let i = 0; i < node.children.length; i += 1) {
          const child = document.createElement('span');
          child.innerHTML = node.children[i].name;
          child.onmouseover = this.clickNode.bind(this, node.children[i]);
          child.onclick = this.getData.bind(this, node.children[i]);
          if (node.children[i].children.length > 0) {
            child.classList.add('tree-input-arrow');
          }
          child.classList.add('tree-input-node');
          panel.appendChild(child);
        }
        panel.classList.add('tree-input-popout-panel');
        root.appendChild(panel);
      }
    }, 300),
  },
};
</script>

<style lang="scss">
#tree-input-popout {
  background: snow;
  position: absolute;
}
#tree-input {
  &:hover {
    cursor: pointer;
  }
  &:read-only{
    background: white;
    color: black;
  }
}
.tree-input-popout-panel {
  min-width: 6em;
  max-width: 20em;
  color: #777;
  position: relative;
  display: inline-block;
  vertical-align: top;
  border-right: 1px solid silver;
  &:last-child {
    border-right: 0;
  }
  .tree-input-node {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    padding: 0.4em 1.0em;
    position: relative;
    &:hover {
      cursor: pointer;
      background: #eee;
    }
  }
  .tree-input-arrow {
    &::after {
      content: "▶";
      position: absolute;
      right: 0.2em;
      font-size: 0.8em;
    }
  }
}
</style>
