<template>
  <div class="star-container">
    <span
    class="star"
    v-for="i in len" :key="i"
    :class="{'star-disable': disable && i >= count, 'star-active': !disable}"
    @click="click(i)"
    >
    </span>
  </div>
</template>

<script>
export default {
  props: {
    disable: {
      type: Boolean,
      default: false,
    },
    count: {
      type: Number,
      default: 0,
    },
    len: {
      type: Number,
      default: 5,
    },
  },
  methods: {
    click(index) {
      if (this.disable) {
        return;
      }
      const offset = 6;
      this.$emit('click', offset - index);
    },
  },
};
</script>

<style lang="scss" scoped>
.star-container {
  display: inline-block;
  direction: rtl;
}
.star::after {
  content: '\2605';
}
.star-active:hover ~ .star-active::after, .star-active:hover::after {
  color: goldenrod;
}
.star-disable {
  color: goldenrod;
}

</style>
