<template>
  <div class="flex-column relative" v-loading="loading">
    <div class="bg-2 pure-form flex-row pd-1">
    <input class="flex-item-center" type="text" v-model="keyword" placeholder="Search">
      <h3 v-if="title" class="ml-auto">
        {{ title }}
      </h3>
    </div>
    <div class="pd-1 flex-1 flex-column" >
      <no-data v-if="list.length === 0 && !loading">
      </no-data>
      <ul>
        <li
        class="mt-1"
        v-for="item in list"
        :key="item.content"
        >
        <a :href="item.to">
          {{ item.content }}
        </a>
        </li>
      </ul>
    </div>
    <pager
    :total="total"
    :active="page"
    @click="$emit('click', $event)"
      />
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '',
    },
    list: {
      type: Array,
      default: [],
    },
    total: {
      type: Number,
      default: 0,
    },
    page: {
      type: Number,
      default: 1,
    },
    loading: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      keyword: '',
    };
  },
  watch: {
    keyword(value) {
      this.$emit('search', value);
    },
  },
};
</script>