export default (ms) => {
  const now = Date.now();
  const prev = new Date(ms).getTime();
  let diff = now - prev;

  // recently
  // horus ago
  // days ago
  // months ago
  // years ago
  const HOUR = 60 * 60 * 1000;
  const DAY = HOUR * 24;
  const MONTH = DAY * 30;
  const YEAR = MONTH * 12;

  const hours = Math.floor(diff / HOUR);
  const days = Math.floor(diff / DAY);
  const months = Math.floor(diff / MONTH);
  const years = Math.floor(diff / YEAR);

  let result = '最近';

  if (hours) {
    result = `${hours} 小时前`;
  }
  if (days) {
    result = `${days} 天前`;
  }
  if (months) {
    result = `${months} 月前`;
  }
  if (years) {
    result = `${years} 年前`;
  }

  return result;
}