<template>
  <div class="flex-row pager-container flex-center">
    <span class="pager pager-first" v-if="hasFirst && showFirst" @click="click(1)">
      first
    </span>
    <div v-if="hasFirst && showFirstDot" class="pager pager-dot">...</div>
    <span
      :class="{'pager-active': x === active}"
      class="pager"
      v-for="x in pages"
      :key="x"
      @click="click(x)">
      {{ x }}
    </span>
    <div v-if="hasLast && showLastDot" class="pager pager-dot">...</div>
    <span class="pager pager-last" v-if="hasLast && showLast" @click="click(last)">
      last
    </span>
  </div>
</template>

<script>
export default {
  props: {
    active: {
      type: Number,
      default: 1,
    },
    total: {
      type: Number,
      required: true,
    },
    len: {
      type: Number,
      default: 20,
    },
    offset: {
      type: Number,
      default: 1,
    },
    showFirst: {
      type: Boolean,
      default: true,
    },
    showLast: {
      type: Boolean,
      default: true,
    },
  },
  watch: {
    total(value) {
      this.last = Math.floor(value / this.len);
      if (value % this.len !== 0) {
        this.last += 1;
      }
    },
  },
  computed: {
    showFirstDot() {
      return this.active - this.offset - 1 > 1;
    },
    showLastDot() {
      return this.last - (this.active + this.offset) > 1;
    },
    hasLast() {
      return this.active + this.offset < this.last;
    },
    hasFirst() {
      return this.active - this.offset > 1;
    },
    pages() {
      const results = [];
      for (let i = this.active - 1; i >= this.active - this.offset && i >= 1; i -= 1) {
        results.push(i);
      }
      results.push(this.active);
      for (let i = this.active + 1; i <= this.active + this.offset && i <= this.last; i += 1) {
        results.push(i);
      }
      return results;
    },
  },
  data() {
    return {
      last: 1,
    };
  },
  methods: {
    click(page) {
      this.$emit('click', page);
    },
  },
};
</script>

<style lang="scss" scoped>
.pager {
  display: inline-block;
  min-width: 2em;
  line-height: 1.6em;
  color: #777;
  font-size: 0.8em;
  text-align: center;
  &:hover {
    color: black;
    cursor: pointer;
  }
}
.pager-active {
  color: black;
}

</style>
